/* Button.css */

button {
	display: inline-block;
	padding: 10px 20px;
	background-color: #70a03a;
	color: #ffffff;
	border: none;
	font-size: 16px;
	cursor: pointer;
	border-radius: 8px;
	transition: background-color .5s;
}

button:hover {
	background-color: #c1f590;
}

button:active {
	background-color: #297910;
}
